.favourite{
  margin-top: 2.56em;
  display: grid;
  grid-template-columns: repeat(4,calc(25% - 1.875em * 3 / 4));
  gap: 1.875em;
}

.favourite-nav{
  margin-top: 3.81em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__selects{
    display: flex;
  }
  .m-select{
    @include lastM(0 .56em 0 0);
  }
}

.m-select{
  .nice-select{
    width: auto;
    color: $black;
    padding: 0 1.14em;
    height: 2.36em;
    border-radius: 0.21em;
    border: 1px solid $black;
    font-size: .875em;
    font-weight: 700;
    display: flex;
    align-items: center;
    justify-content: space-between;
    &:before{
      content: '';
      flex-shrink: 0;
      margin-left: 1em;
      width: 1em;
      height: 1em;
      order: 2;
      background-image: url(../img/select-arrow.svg);
      background-repeat: no-repeat;
      background-position: center;
      background-size: 100% auto;
    }
  }
  select{
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    -o-appearance: none;
    appearance: none;
    &::-ms-expand {
      display: none; // IE
    }



  }
  .list{
    border-radius: 0;
    border: 1px solid $black;
  }
  .option{
    font-size: .875em;
    border: none;
    &:not(:last-child){
      border-bottom: 1px solid $black;
    }
    &.selected{
      background-color: $black !important;
      color: #fff;
    }
  }
}


@media screen and (max-width: 992px){
  .favourite-nav{
    margin-top: 1.875em;
  }

  .favourite{
    margin-top: 1.75em;
    grid-template-columns: repeat(3,calc(100% / 3 - 1em / 3));
    gap: 1em .5em;
  }
}


@media screen and (max-width: 576px){
  .favourite-nav{
    @include grid100gap(.5em);
  }

  .favourite{
    grid-template-columns: repeat(2,calc(50% - .5em));
  }
}
