.delivery{
  padding: 4.06em 0 5em;
  &__desc{
    margin-bottom: 3em;
  }
}

.delivery-variants{
  margin-bottom: 3em;
  padding: 3.125em 0;
  background-color: $gray3;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__item{
    height: 4em;
    max-width: 12.81em;
    @include center;
    img{
      display: block;
      max-width: 100%;
      max-height: 100%;
    }
  }
  &._payment{
    .delivery-variants__item{
      max-width: 11em;
    }
    ._small{
      height: 2.6875em;
    }
  }
}


@media screen and (max-width: 992px){
  .delivery{
    padding: 2em 0;
    &__desc{
      margin-bottom: 1.25em;
    }
  }

  .delivery-variants{
    padding: 1em 0;
    margin-bottom: 1.25em;
    &__inner{
      display: grid;
      grid-template-columns: repeat(6,calc(100% / 6 - 5em / 6));
      gap: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .delivery-variants{
    &__inner{
      grid-template-columns: repeat(3,calc(100% / 3 - 1em));
      gap: 1.5em;
    }
  }
}
