.faq{
  padding-bottom: 5em;
  &__content,
  .tabs{
    margin-top: 3.375em;
  }
  &__items{
    @include grid100gap(.5em);
  }
}

.tabs{
  &__dropdown{
    display: flex;
  }
  &__select{
    display: none;
  }
  .m-link{
    cursor: pointer;
    transition: all .3s;
    &._active{
      color: $primary;
      text-decoration: underline;
    }
  }
}

.qa-card{
  border-radius: 0.25em;
  background-color: $gray3;
  &__show{
    cursor: pointer;
    padding: 1.875em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__btn{
    width: 1.5em;
    flex-shrink: 0;
    margin-left: 1em;
    svg{
      width: 100%;
      height: auto;
    }
  }
  &__hidden{
    padding: 0 1.875em 1.875em;
    margin-top: -.375em;
    display: none;
  }
  &._active{
    background-color: $blue3;
    color: #fff;
    .qa-card__btn{
      svg{
        transform: rotate(-180deg);
      }
      path{
        stroke: #fff;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .faq{
    padding-bottom: 1.5em;
    &__content,
    .tabs{
      margin-top: 1.25em;
    }
  }

  .tabs{
    display: flex;
    position: relative;
    z-index: 3;
    &._show-dropdown{
      .tabs__dropdown{
        opacity: 1;
        pointer-events: unset;
      }
    }
    &__select{
      min-width: 8em;
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 0 .375em 0 1em;
      height: 1.875em;
      background-color: $black;
      color: #fff;
      border-radius: .1875em;
      svg{
        width: 1.1875em;
        height: auto;
        margin-left: 1em;
      }
    }
    &__current{
      font-weight: 700;
      span{
        font-size: .8125em;
        line-height: 120%;
      }
    }
    &__dropdown{
      position: absolute;
      top: 100%;
      left: 0;
      min-width: 100%;
      margin-top: .25em;
      display: block;
      z-index: 3;
      background-color: #fff;
      border-radius: .1875em;
      box-shadow: 0 .25em 1.25em 0 rgba(23,27,32,.17);
      transition: all .3s;
      opacity: 0;
      pointer-events: none;
      .m-link{
        min-height: 1.625em;
        text-decoration: none;
        display: flex;
        align-items: center;
        padding: .125em 1em;
        span{
          font-size: .75em;
          line-height: 110%;
        }
      }
    }
  }

  .qa-card{
    &__show{
      padding: .75em 1em;
    }
    &__hidden{
      padding: 0 1em .75em;
    }
    .text18{
      font-size: 1em;
    }
  }
}
