.compare{
  display: grid;
  grid-template-columns: 19.8125em calc(100% - 19.8125em);
  border: 1px solid #EEE;
  margin-top: 3.38em;
}

.compare-nav{
  margin-top: 4.5em;
  display: flex;
  justify-content: space-between;
  align-items: center;
  &__tabs{
    display: flex;
  }
  &__btn{
    padding: 0 2.47em;
    border-radius: 0;
    &:not(._active):not(:hover){
      background-color: $gray3;
      color: $gray;
    }
  }
}

.compare-sidebar{
  border-right: 1px solid #EEE;
  &__top{
    padding: 1.25em;
    height: 38.125em;
  }
}

.compare-props{
  padding: 6.1em 0 2em;
  position: relative;
  border-top: 1px solid #EEE;
  @include grid100gap(.625em);
  &__title{
    position: absolute;
    top: 0;
    left: 0;
    padding: 2.56em 1.25em;
  }
}

.compare-prop{
  padding: 0 1.25em;
  display: flex;
  align-items: center;
  height: 2.5em;
  transition: all .3s;
  span{
    font-size: 1em;
    line-height: 119%;
  }
  &._active{
    background-color: $gray3;
    font-weight: 700;
  }
}

.compare-slider{
  overflow: hidden;
  .swiper{
    &-container{
      width: calc(100% + 1px);
      height: 100%;
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      width: 25%;
      height: auto;
    }
  }
}

.compare-item{
  border-right: 1px solid #EEE;
  height: 100%;
  &__top{
    padding: 1.25em;
    height: 38.125em;
  }
  .remove-btn{
    margin-left: 1.25em;
  }
  .product-card{
    &__img{
      padding-bottom: 140%;
    }
  }
  .compare-prop{
    justify-content: center;
    text-align: center;
    font-weight: 700;
    &._active{
      color: $primary;
    }
  }
}


@media screen and (max-width: 992px){
  .compare-nav{
    margin-top: 1.875em;
    &__btn{
      padding: 0 1em;
      height: 2em;
    }
  }

  .compare{
    width: calc(100% + 1.875em);
    border-right: none;
    border-left: none;
    margin-top: 1.75em;
    margin-left: -.9375em;
    grid-template-columns: 12em calc(100% - 12em);
  }

  .compare-props{
    padding-top: 3.5em;
    &__title{
      padding: .75em;
    }
  }

  .compare-prop{
    padding: 0 .75em;
    height: 1.25em;
    span{
      font-size: .75em;
    }
  }

  .compare-sidebar{
    &__top{
      height: 25em;
      padding: .75em;
    }
    .text22{
      font-size: 1em;
    }
  }

  .compare-slider{
    .swiper{
      &-slide{
        width: 12em;
      }
    }
  }

  .compare-item{
    .favourite-btn{
      margin-bottom: .625em;
    }
    .remove-btn{
      margin: 0;
    }
    .product-card{
      padding: 0;
    }
    &__top{
      padding: .75em;
      height: 25em;
    }
  }
}


@media screen and (max-width: 576px){
  .compare-nav{
    flex-direction: column;
    align-items: flex-start;
    &__tabs{
      display: block;
      width: 100%;
    }
    &__btn{
      margin: 0 0 .5em;
    }
  }

  .compare{
    font-size: .875em;
  }
}
