@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
@import "components/_spinner.scss";
@import "components/_sliders.scss";
.hint{
  position: relative;
  &:hover{
    .hint__tooltip{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__tooltip{
    position: absolute;
    bottom: 90%;
    left: 50%;
    transform: translateX(-50%);
    padding-bottom: .5em;
    pointer-events: none;
    opacity: 0;
    transition: all .3s;
    &-inner{
      background-color: #fff;
      width: 20em;
      padding: 1em;
      box-shadow: 0 4px 19px 0 rgba(23,27,32,.17);
      border-radius: .1875em;
      z-index: 3;
    }
  }
}


.swiper-button{
  width: 2.5em;
  height: 2.5em;
  border-radius: 50%;
  border: 1px solid #D7D7D7;
  @include center;
  svg{
    width: 1.25em;
    height: auto;
  }
  &.swiper-button-disabled{
    //pointer-events: unset;
  }
  &:hover{
    border-color: $black;
    path{
      stroke: $black;
    }
  }
}

.swiper-pagination{
  display: flex;
  align-items: center;
  justify-content: center;
  &._static{
    position: static;
    margin-top: 4.38em;
  }
  &-bullet{
    width: 0.375em;
    height: 0.375em;
    margin: 0 .4375em !important;
    background-color: rgba($gray,.5);
    opacity: 1;
    &-active{
      width: 0.625em;
      height: 0.625em;
      background-color: #5B5B5B;
    }
  }
}

.tab-content{
  display: none;
  &._active{
    display: block;
  }
}

.grid-column2,
.grid-column3,
.grid-column4,
.grid-column5,
.grid-column6{
  display: grid;
  gap: 1.875em;
}

.grid-column2{
  grid-template-columns: repeat(2,calc(50% - .9375em));
}

.grid-column3{
  grid-template-columns: repeat(3,calc(100% / 3 - 1.25em));
}

.grid-column4{
  grid-template-columns: repeat(4,calc(25% - 1.875em * 3 / 4));
}

.grid-column5{
  grid-template-columns: repeat(5,calc(20% - 1.5em));
}

.grid-column6{
  grid-template-columns: repeat(6,calc(100% / 6 - 1.875em * 5 / 6));
}


@media screen and (max-width: 992px){
  .grid-full-tablet{
    grid-template-columns: 100%;
    gap: .625em;
  }

  .grid-column2-tablet{
    grid-template-columns: repeat(2,calc(50% - .3125em));
    gap: .625em;
  }

  .grid-column3-tablet{
    grid-template-columns: repeat(3,calc(100% / 3 - 1.25em / 3));
    gap: .625em;
  }

  .grid-column4-tablet{
    grid-template-columns: repeat(4,calc(25% - 1.875em / 4));
    gap: .625em;
  }

  .swiper{
    &-pagination{
      &._static{
        margin-top: 1.56em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .grid-column2-mob{
    grid-template-columns: repeat(2,calc(50% - .3125em));
    gap: .625em;
  }

  .grid-full-mob{
    grid-template-columns: 100%;
  }
}
