.requisites{
  padding-bottom: 3.69em;
  &__props{
    margin-top: 3.125em;
    max-width: 48.75em;
  }
  &__docs{
    display: flex;
    margin-top: 2.875em;
    .doc-item{
      min-width: 12.3125em;
    }
  }
}

.prop-item{
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #EFEFEF;
  padding: 1em 0;
  &:last-child{
    border-bottom: none;
  }
  &__desc{
    margin-left: 1em;
    text-align: right;
  }
}


@media screen and (max-width: 992px){
  .requisites{
    padding-bottom: 1.5em;
    &__props{
      margin-top: 1.25em;
    }
    &__docs{
      margin-top: 1.75em;
    }
  }

  .prop-item{
    padding: .75em 0;
    .text16{
      font-size: .875em;
    }
  }
}


@media screen and (max-width: 576px){
  .requisites{
    &__docs{
      display: block;
    }
    .doc-item{
      justify-content: center;
    }
  }
}
