.footer-info{
  background: #FCFCFC;
  padding: 3.94em 0 4.31em;
  &__inner{
    display: flex;
    justify-content: space-between;
  }
}

.footer-payment{
  max-width: 23.8125em;
  .text18{
    ._view2{
      display: none;
    }
  }
  &__items{
    margin-top: 2em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
    gap: 1.25em;
  }
  &__item{
    display: flex;
    align-items: center;
    height: 3.4375em;
    img{
      max-width: 100%;
      max-height: 100%;
      transition: all .3s;
    }
    &:not(:hover){
      img{
        opacity: 0.4;
        mix-blend-mode: luminosity;
      }
    }
  }
}

.footer-advantages{
  width: 30em;
  .advantages__grid{
    margin-top: 2.25em;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1.25em;
    .header-advantage__icon{
      width: 2.125em;
      margin-right: .625em;
    }
  }

}

.footer-top,
.footer-bottom{
  &__inner{
    display: grid;
    grid-template-columns: 23.875em 28em calc(100% - 71.875em);
    gap: 10em;
  }
}

.footer-top{
  margin-top: 5.37em;
  padding-bottom: 3.63em;
  &__col3{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
  }
}

.footer{
  &__logo{
    width: 20.875em;
    display: block;
    img{
      width: 100%;
      display: block;
    }
  }
  .socials{
    margin-top: 6.63em;
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: 6.31em;
  }
  &__link{
    color: rgba(31, 33, 35, 0.80);
    line-height: 147%;
    text-decoration: underline;
    opacity: .4;
    @include lastM(0 0 1.25em);
    span{
      font-size: .9375em;
    }
    &:hover{
      opacity: 1;
    }
  }
  &__by{
    width: 8.93em;
    display: block;
    &:not(:hover){
      opacity: .5;
    }
    img{
      width: 100%;
      display: block;
    }
  }
  &__c{
    font-size: .9375em;
    line-height: 146%;
    opacity: .4;
    color: rgba(31, 33, 35, 0.80);
  }
}

.footer-nav{
  display: flex;
  justify-content: space-between;
  .text18{
    line-height: 153%;
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: 2.625em;
  }
  &__link{
    font-size: 1em;
    line-height: 150%;
    @include lastM(0 0 1.5em);
  }
}

.footer-contacts{
  @include grid100gap(.5em);
  margin-bottom: 1em;
  &__phone{
    font-size: 1.25em;
    line-height: 195%;
    font-weight: 700;
  }
  .text16{
    line-height: 144%;
    max-width: 16em;
  }
}

.footer-bottom{
  padding-bottom: 2.25em;
  &__inner{
    align-items: center;
  }
}

.scroll-up {
  position: fixed;
  width: 5.625em;
  height: 5.625em;
  border-radius: 50%;
  bottom: 3.125em;
  right: 2em;
  z-index: 8;
  pointer-events: none;
  opacity: 0;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: .875em 0;
  color: #fff;
  font-weight: 500;
  line-height: 230%;
  &--active {
    opacity: 1;
    pointer-events: unset;
  }
  svg{
    width: 1.5em;
    height: auto;
    margin-bottom: -.125em;
  }
  span{
    font-size: 1em;
  }
}


@media screen and (max-width: 992px){
  .footer-info{
    background-color: transparent;
    padding: 2.5em 0 2.125em;
  }

  .footer-advantages{
    display: none;
  }

  .footer-payment{
    .text18{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
        line-height: inherit;
      }
    }
    &__items{
      margin-top: 1em;
      grid-template-columns: repeat(3,calc(100% / 3 - 1em / 3));
      gap: .5em;
    }
    &__item{
      height: 2.5625em;
      max-width: 6em;
    }
  }

  .footer-top{
    margin: 0;
    padding: 2.5em 0 1.5em;
    background-color: $gray3;
    &__inner{
      grid-template-columns: 100%;
    }
    &__col1{
      display: flex;
      flex-direction: column;
      align-items: center;
    }
    &__col2,
    &__col3{
      display: none;
    }
  }

  .footer{
    &__logo{
      width: 17.3em;
    }
    .socials{
      margin-top: 1.875em;
    }
    &__links{
      margin-top: 1.875em;
      align-items: center;
    }
    &__link{
      opacity: 1;
      color: $black;
      @include lastM(0 0 .81em);
      span{
        font-size: .875em;
        line-height: 157%;
      }
    }
    &__by{
      margin: 0 auto;
    }
    &__c{
      font-size: 0.875em;
      color: $black;
    }
  }

  .footer-bottom{
    background-color: $gray3;
    padding-bottom: 5.25em;
    text-align: center;
    &__inner{
      grid-template-columns: 100%;
      gap: 1.5em;
    }
  }

  .scroll-up{
    width: 3.25em;
    height: 3.25em;
    right: 1.2em;
    bottom: 7em;
    span{
      display: none;
    }
    svg{
      margin: 0;
    }
  }
}


@media screen and (max-width: 576px){
  .footer-info{
    &__inner{
      @include grid100gap(1.56em);
    }
  }

  .footer-payment{
    padding: .94em 0;
    .text18{
      text-align: center;
    }
  }
}
