.header{
  &__phone{
    white-space: nowrap;
  }
  &__login{
    display: flex;
    align-items: center;
    color: $black;
    svg{
      width: 1.625em;
      height: auto;
      margin-right: .625em;
    }
    &:hover{
      color: $primary;
      path{
        stroke: $primary;
      }
    }
  }
}

.header-level1,
.header-level2,
.header-level3,
.header-level4{
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.header-level1{
  padding: .3125em 0;
  background-color: $black;
  color: #fff;
  .socials{
    &__item{
      width: 1.5em;
      @include lastM(0 .625em 0 0);
    }
  }
}

.header-city{
  display: flex;
  align-items: center;
  color: #fff;
  &:hover{
    color: $primary;
    path{
      fill: $primary;
    }
  }
  svg{
    width: 1em;
    height: auto;
    margin-right: .31em;
  }
}

.header-level2{
  padding: .625em 0;
  margin-top: .625em;
  &__inner{
    position: relative;
  }
  .search-field{
    flex-grow: 1;
    max-width: 41.625em;
  }
}

.header-logo{
  display: flex;
  align-items: center;
  &__link{
    width: 16.125em;
    flex-shrink: 0;
    margin-right: 1.875em;
    img{
      width: 100%;
      display: block;
    }
    ._view2{
      display: none;
    }
  }
  &__text{
    max-width: 10em;
  }
}

.header-action{
  display: flex;
  align-items: center;
  &__btn{
    width: 1.75em;
    flex-shrink: 0;
    position: relative;
    @include lastM(0 1.875em 0 0);
    @include center;
    &._desktop-mob{
      display: none;
    }
    svg{
      width: 100%;
      height: auto;
    }
    .pin{
      top: -.1em;
      right: -.31em;
    }
  }
}

.header-level3{
  padding: .75em 0;
}

.catalog-btn{
  padding: 0 1.4em;
  .catalog-btn__icon{
    font-size: 1em;
  }
  &__icon{
    position: relative;
    margin-right: .94em;
    width: 1.25em;
  }
  ._view2{
    opacity: 0;
  }
  svg{
    width: 100%;
    height: auto;
    @include centerAbsolute;
  }
  span{
    text-decoration: underline;
  }
  &:hover{
    path{
      stroke: #fff;
    }
  }
}

.nav{
  display: flex;
  align-items: center;
  &__sales span,
  &__link{
    font-size: 1em;
    line-height: 119%;
    font-weight: 700;
    text-decoration: underline;
  }
  &__link{
    margin-left: 3.11em;
  }
  &__sales{
    display: flex;
    align-items: center;
    color: $primary;
    svg{
      width: 1.1875em;
      height: auto;
      margin-right: .625em;
    }
    &:hover{
      color: $black;
      path{
        fill: $black;
      }
    }
  }
}

.header-level4{
  padding: .625em 0;
  background-color: $gray3;
  margin-top: .3125em;
}

.header-advantage{
  display: flex;
  align-items: center;
  &__icon{
    width: 2.5em;
    flex-shrink: 0;
    margin-right: .625em;
    img{
      width: 100%;
      display: block;
    }
  }
  a{
    text-decoration: underline;
  }
}

.menu-top,
.header-search-mob,
.menu-address,
.burger{
  display: none;
}


@media screen and (max-width: 992px){
  .header{
    &__phone,
    &__login{
      display: none;
    }
    &._open-mob-menu{
      .header-level3{
        transform: translateX(0);
      }
    }
  }

  .header-level1,
  .header-level4{
    display: none;
  }

  .header-level2{
    padding: .75em 0 .625em;
    background-color: $black;
    color: $gray;
    margin: 0;
    .search-field{
      display: none;
    }
  }

  .header-logo{
    flex-direction: column;
    width: 100%;
    &__link{
      width: 10.375em;
      margin: 0 0 .25em;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__text{
      max-width: unset;
      .text15{
        font-size: .75em;
        line-height: 125%;
      }
    }
  }

  .header-action{
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 1em 1.875em;
    background: #F5F5F5;
    z-index: 20;
    justify-content: center;
    &__btn{
      width: unset;
      height: 1.375em;
      @include lastM(0 3em 0 0);
      &._hide-mob{
        display: none;
      }
      &._desktop-mob{
        display: flex;
      }
      svg{
        width: auto;
        height: 100%;
      }
    }
    .pin{
      height: .6875em;
      min-width: .6875em;
      span{
        font-size: .4375em;
      }
    }
  }

  .header-level3{
    transition: all .4s;
    transform: translateX(-120%);
    padding: 0;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    z-index: 30;
    background-color: #fff;
    overflow-y: auto;
    .container{
      padding: 0;
    }
    &__inner{
      display: block;
    }
  }

  .nav{
    display: block;
    &__link,
    &__sales{
      padding: 1.06em;
      min-height: 3.56em;
      display: flex;
      align-items: center;
      margin: 0;
      border-bottom: 1px solid #EEE;
    }
  }

  .catalog-btn{
    border: none;
    border-bottom: 1px solid #EEE;
    width: 100%;
    padding: 1.06em;
    min-height: 3.56em;
    justify-content: unset;
    &:hover{
      background-color: transparent;
      color: $black;
      path{
        stroke: $black;
      }
    }
    span{
      text-decoration: none;
      font-size: 1em;
    }
  }

  .header-search-mob{
    padding: 1.25em .94em;
    display: block;
    input{
      font-size: 1em;
      height: 3em;
    }
    .search-field__btn span{
      font-size: .875em;
    }
  }

  .menu-top{
    padding: .625em 1.0625em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: $black;
    color: #fff;
    .header-city{
      color: inherit;
    }
    &__close{
      width: 1.25em;
      flex-shrink: 0;
      svg{
        width: 100%;
        height: auto;
      }
      &:hover{
        path{
          stroke: $primary;
        }
      }
    }
  }

  .menu-address{
    display: block;
    margin-top: 1.875em;
    padding: 0 1.06em 2em;
    &__item{
      padding: 1.25em 0;
      &:not(:first-child){
        border-top: 1px solid #EEE;
      }
    }
    &__top{
      display: flex;
      align-items: center;
      margin-bottom: .25em;
      .text16{
        margin-left: 1.25em;
      }
    }
    &__btn{
      width: 100%;
      margin-top: .625em;
    }
  }

  .burger{
    position: absolute;
    right: .25em;
    top: 50%;
    transform: translateY(-50%);
    @include center;
    width: 1.875em;
    svg{
      width: 100%;
      height: auto;
    }
  }
}


@media screen and (max-width: 576px){
  .header-action{
    justify-content: space-between;
  }
}
