.config{
  padding: 5.81em 0 6.69em;
  background-color: $gray3;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__pagination{
    color: $gray;
    ._current{
      color: $black;
    }
  }
  &__content{
    margin-top: 3.56em;
  }
  &__toggle{
    display: none;
  }
}

.config-step{
  display: none;
  &._active{
    display: block;
  }
  &__next{
    font-weight: 700;
    span{
      font-size: 1.25em;
      line-height: 120%;
    }
    svg{
      width: 1.5em;
      height: auto;
      margin-left: 4em;
    }
  }
  &__nav{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 1.56em;
    ._next{
      display: none;
    }
    &._hide-desktop{
      display: none;
    }
  }
}

.config-card{
  input {
    &:checked ~ .config-card__inner{
      background: $black;
      color: #fff;
      label {
        &::before {
          background-color: $primary;
          border-color: $primary;
          background-image: url(../img/check.svg);
        }
      }
    }
  }
  &__inner{
    transition: all .3s;
    padding: 0 1.25em 1.875em;
    border-radius: 0.1875em;
    background: linear-gradient(180deg, rgba(223, 223, 223, 0.50) 0%, #DFDFDF 100%);
    height: 100%;
  }
  &__img{
    display: flex;
    justify-content: center;
    height: 15em;
    margin-bottom: 1.875em;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  .m-check{
    label{
      span{
        font-size: 1.125em;
      }
      &:before{
        top: .125em;
      }
    }
  }
}

.config-modal{
  &__close,
  &__bg,
  &__title {
    display: none;
  }
}


@media screen and (max-width: 992px){
  .config-modal{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 20;
    padding: 1em 0;
    opacity: 0;
    pointer-events: none;
    &__bg{
      background: rgba($black, 0.80);
      position: fixed;
      width: 100%;
      height: calc(100 * var(--vh));
      top: 0;
      left: 0;
      display: none;
    }
    &__dialog{
      border-radius: .1875em;
      background: #fff;
      width: 31.25em;
      max-width: calc(100% - 1em);
      position: relative;
      z-index: 3;
      padding: 1.56em .94em;
      margin: 0 auto;
    }
    &__close{
      position: absolute;
      top: .56em;
      right: .5em;
      width: 1.25em;
      @include center;
      svg{
        width: 100%;
        height: auto;
      }
      &:hover{
        path{
          stroke: $black;
        }
      }
    }
    &__title{
      display: block;
      text-align: center;
      padding: 0 1em;
    }
    &._active {
      opacity: 1;
      pointer-events: unset;
      .config-modal__bg {
        display: block;
      }
    }
  }

  .config{
    padding: 2.81em 0;
    background-color: #fff;
    &__content{
      margin-top: 1.56em;
    }
    &__pagination{
      display: none;
    }
    &__top{
      justify-content: center;
      text-align: center;
      padding: 0 2em;
    }
    &__toggle{
      display: flex;
      margin: 1.56em auto 0;
    }
  }

  .config-step{
    &__grid{
      grid-template-columns: 100%;
      gap: .44em;
    }
    &__next{
      display: none;
    }
    &__nav{
      display: grid;
      grid-template-columns: 7.6875em calc(100% - 7.6875em - 1.56em);
      gap: 1.56em;
      &._hide-desktop{
        display: flex;
        justify-content: center;
        gap: unset;
      }
      ._next{
        display: flex;
      }
    }
  }

  .config-card{
    &__inner{
      padding: 0 .625em;
      display: grid;
      grid-template-columns: 4.4375em calc(100% - 5.4375em);
      gap: 1em;
      align-items: center;
      background: $gray3;
    }
    &__img{
      margin: 0;
      height: 4.75em;
    }
    .m-check{
      label{
        span{
          font-size: .9375em;
        }
        &:before{
          top: 0;
        }
      }
    }
  }
}
