.main{
  background-color: $black;
  color: #fff;
  margin-top: -1.69em;
  padding: 4.69em 0 5.88em;
  position: relative;
  min-height: 31.375em;
  display: flex;
  flex-direction: column;
  &__inner,
  &__content,
  .container{
    flex-grow: 1;
    display: flex;
    flex-direction: column;
  }
  &__content{
    position: relative;
    z-index: 2;
    max-width: 58.875em;
  }
  &__logo,
  &__img{
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    width: 50%;
    display: flex;
    overflow: hidden;
  }
  &__img{
    img{
      width: auto;
      height: 100%;
      display: block;
    }
  }
  &__desc{
    margin-top: 2.5em;
    max-width: 48.875em;
  }
  &__links{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin-top: auto;
    padding-top: 1.9em;
    border-top: 1px solid rgba(255, 255, 255, 0.06);
    .m-link{
      margin: 1.56em 2.5em 0 0 !important;
    }
    &._type2{
      @media screen and (min-width: 993px){
        .m-link{
          margin-right: 1.56em !important;
        }
      }
    }
  }
  &__top{
    display: flex;
    align-items: center;
  }
  &__count{
    flex-shrink: 0;
    margin-left: 1.25em;
    padding: 0 .5em;
    border-radius: 0.1875em;
    border: 1px solid rgba(255, 255, 255, 0.30);
    color: rgba(#fff,.3);
    height: 2.125em;
    @include center;
  }
  &__logo{
    justify-content: center;
    align-items: center;
    img{
      width: 29.5625em;
    }
  }
}

.m-link{
  font-size: 1em;
  line-height: 119%;
  font-weight: 700;
  text-decoration: underline;
  @include lastM(0 1.56em 0 0);
  &:hover{
    text-decoration: none;
    color: $primary;
  }
}

.juridical{
  padding: 4.06em 0 5em;
}

.text{
  h3{
    font-weight: 700;
    margin-bottom: .857em;
  }
  p{
    line-height: 162.5%;
    margin-bottom: 1em;
    &:last-child{
      margin-bottom: 0;
    }
  }
  hr{
    display: block;
    height: 2em;
    opacity: 0;
  }
  img{
    width: 100%;
    display: block;
    margin: 2em 0 1em;
    &:first-child{
      margin-top: 0;
    }
    &:last-child{
      margin-bottom: 0;
    }
  }
}

.docs{
  margin-top: 2.5em;
  display: grid;
  grid-template-columns: 1fr 1fr 2fr;
  gap: .94em;
  max-width: 61.9375em;
}

.doc-item{
  display: flex;
  align-items: center;
  padding: 1em 1.5em;
  border-radius: 0.1875em;
  border: 1px solid $black;
  &__icon{
    width: 1.5em;
    flex-shrink: 0;
    margin-right: .84em;
    position: relative;
    svg{
      width: 100%;
      height: auto;
      display: block;
    }
  }
  &__content{
    @include grid100gap(.12em);
  }
  .spinner{
    opacity: 0;
    position: absolute;
    width: 150%;
    height: auto;
  }
  &:hover,
  &._loading{
    background-color: $primary;
    border-color: $primary;
    color: #fff;
    .gray-text{
      color: #fff;
    }
    .doc-item__icon{
      path{
        stroke: #fff;
      }
    }
  }
  &._loading{
    .file-icon{
      opacity: 0;
    }
    .spinner{
      z-index: 2;
      opacity: 1;
      @include centerAbsolute;
    }
  }
  &._disabled{
    color: $gray;
    border-color: $gray;
    pointer-events: none;
    .doc-item__icon svg{
      opacity: .5;
    }
  }
}



@media screen and (max-width: 992px){
  .main{
    min-height: auto;
    margin-top: -.5em;
    padding: 2em 0;
    &__img{
      display: none;
    }
    &__desc{
      margin-top: 1em;
      .text16{
        font-size: .875em;
      }
    }
    &__links{
      padding-top: .5em;
      margin-top: 1.5em;
      .m-link{
        margin: 1.25em 1.25em 0 0 !important;
        font-size: .75em;
      }
    }
    &__logo{
      order: -1;
      position: static;
      width: 10em;
      margin: 0 0 1em;
      img{
        width: 100%;
        display: block;
      }
    }
  }

  .juridical{
    padding: 2em 0;
  }

  .text{
    h3{
      margin-bottom: .5em;
    }
    p{
      font-size: .875em;
      line-height: 120%;
    }
    hr{
      height: .75em;
    }
  }

  .docs{
    grid-template-columns: repeat(2,calc(50% - .3125em));
    gap: .625em;
    margin-top: 1.5em;
  }

  .doc-item{
    padding: .5em .625em;
  }
}


@media screen and (max-width: 576px){
  .docs{
    grid-template-columns: 100%;
  }

  .main{
    &__top{
      display: block;
      .text42,
      .text35{
        display: inline-block;
      }
    }
    &__count{
      display: inline-flex;
      margin-left: .25em;
    }
  }
}
