.contacts{
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__items{
    margin-top: 3.875em;
    display: flex;
    justify-content: space-between;
  }
  .map{
    margin-top: 3.875em;
  }
}

.city-tabs{
  display: flex;
}

.city-tab{
  padding: 0 1.5625em;
  height: 3.125em;
  background-color: $black;
  color: #6E7378;
  display: flex;
  align-items: center;
  font-weight: 700;
  transition: all .3s;
  cursor: pointer;
  &._active{
    pointer-events: none;
  }
  &._active,
  &:hover{
    color: #fff;
    background-color: $primary;
    path{
      fill: #fff;
    }
  }
  svg{
    width: 1em;
    height: auto;
    margin-right: .5em;
    flex-shrink: 0;
  }
  span{
    font-size: 1.125em;
    line-height: 120%;
  }
  &:first-child{
    border-top-left-radius: .1875em;
    border-bottom-left-radius: .1875em;
  }
  &:last-child{
    border-top-right-radius: .1875em;
    border-bottom-right-radius: .1875em;
  }
}

.contact-item{
  max-width: 16.3125em;
  .text18{
    line-height: 150%;
  }
  &__desc{
    margin-top: .94em;
  }
}

.socials{
  display: flex;
  align-items: center;
  &__item{
    width: 2em;
    flex-shrink: 0;
    @include lastM(0 .875em 0 0);
    img{
      transition: inherit;
      width: 100%;
      display: block;
    }
    &:hover{
      img{
        transform: translateY(-.5em);
      }
    }
  }
}

.map{
  height: 50em;
}

.shops{
  padding: 5em 0;
  &.pt0{
    padding-top: 0;
  }
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__link{
    padding: 0 2em;
  }
  &__grid{
    margin-top: 3.125em;
  }
}

.shop-card{
  &__slider{
    position: relative;
    overflow: hidden;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(180deg, rgba(31, 33, 35, 0.00) 60.8%, #1F2123 100%);
      z-index: 2;
      border-radius: 0.1875em;
      pointer-events: none;
    }
    .swiper{
      &-pagination{
        position: static;
        width: auto;
        &-bullet{
          background-color: rgba(#fff,.5);
          &-active{
            background-color: #fff;
          }
        }
      }
      &-button{
        position: static;
        margin: 0;
        &:hover{
          border-color: #fff;
          background-color: #fff;
        }
      }
    }
  }
  &__img{
    border-radius: 0.1875em;
    @include imgCover(59.1%);
  }
  &__nav{
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    padding: 1.81em 1.38em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    z-index: 3;
  }

  &__content{
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
  }
  &__info{
    margin-top: 2.38em;
    margin-right: 1em;
    @include grid100gap(.625em);
  }
  &__link{
    flex-shrink: 0;
  }
}


@media screen and (max-width: 992px){
  .shops{
    padding: .94em 0;
    margin-top: 1.56em;
    &__grid{
      margin-top: 1.56em;
      grid-template-columns: repeat(2,calc(50% - .75em));
      gap: 1.69em 1.5em;
    }
  }

  .shop-card{
    &__info{
      margin-top: .94em;
      gap: .5em;
      max-width: 16.125em;
    }
    &__nav{
      display: none;
    }
    &__link{
      padding: 0;
      width: 2.625em;
      height: 2.625em;
      background-image: url(../img/arrow-right.svg);
      @include bgImgSize(1.125em);
      span{
        display: none;
      }
    }
  }

  .contacts{
    &__top{
      flex-direction: column;
    }
    .city-tabs{
      margin-top: 1.56em;
    }
    .tab-content{
      padding-top: 21.56em;
      padding-bottom: 3.125em;
      position: relative;
      margin-top: 1.56em;
    }
    .map{
      margin: 0;
      position: absolute;
      top: 0;
      left: .9375em;
      height: 20em;
      width: calc(100% - 1.875em);
    }
    &__items{
      margin-top: 0;
      border-radius: 0.1875em;
      background-color: $gray3;
      padding: 1em;
      display: block;
    }
  }

  .city-tab{
    height: 2.625em;
    padding: 0 1.875em;
    span{
      font-size: .9375em;
    }
  }

  .contact-item{
    display: grid;
    align-items: flex-start;
    grid-template-columns: 7.625em calc(100% - 7.625em - .25em);
    justify-content: space-between;
    max-width: unset;
    &:not(:first-child){
      margin-top: 1.56em;
    }
    .text18{
      font-size: .875em;
      line-height: 143%;
    }
    &__desc{
      margin: 0;
      .text18{
        font-size: .9375em;
        line-height: 133%;
      }
    }
    &._social{
      margin-top: 0 !important;
      gap: 0;
      .text18{
        display: none;
      }
      .socials{
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
      }
    }
  }

  .socials{
    &__item{
      width: 1.75em;
      @include lastM(0 .625em 0 0);
    }
  }
}


@media screen and (max-width: 576px) {
  .shops {
    &__grid {
      grid-template-columns: 100%;
    }
  }
}
