.shops-map{
  padding-top: 5em;
}

.shop-item{
  padding-bottom: 4.88em;
  &__grid{
    margin-top: 2.875em;
  }
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__link{
    margin-left: 1em;
    img{
      width: 1.875em;
      margin-right: .75em;
      flex-shrink: 0;
    }
  }
  .text{
    margin-top: 1.5em;
    @include grid100gap(1.5em);
    h3{
      margin-top: .3em;
    }
    h3,
    p{
      margin-bottom: 0;
    }
    hr{
      height: .5em;
    }
  }
}

.shops-page{
  padding-bottom: 7em;
  border-bottom: 1px solid #EFEFEF;
  .shops__grid{
    margin-top: 2.44em;
  }
  &__text{
    margin-top: 2.5em;
  }
}


@media screen and (max-width: 992px){
  .shops-map{
    padding: .94em 0 0;
    margin-top: 1.56em;
  }

  .shop-item{
    padding-bottom: 2em;
    &__grid{
      margin-top: 1.75em;
    }
    .text{
      gap: .75em;
    }
  }

  .shops-page{
    &__text{
      margin-top: 1em;
    }
    .shops__grid{
      margin-top: 1.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .shop-item{
    &__top{
      flex-direction: column;
      align-items: flex-start;
    }
    &__link{
      margin: .75em 0 0;
    }
  }
}
