.banners{
  overflow: hidden;
  padding: 1.94em 0;
  .swiper{
    &-container{
      overflow: hidden;
      width: calc(100% + .25em);
      margin-left: -.125em;
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
    }
    &-pagination{
      margin-top: 1.25em;
    }
    &-button{
      &-prev{
        left: -3.875em;
      }
      &-next{
        right: -3.875em;
      }
    }
  }
}

.banners-slider{
  position: relative;
}

.banner{
  min-height: 46.875em;
  padding: 5.12em 6.75em 4.06em;
  border-radius: 0.0625em;
  background-color: $gray3;
  display: flex;
  flex-direction: column;
  position: relative;
  &__img{
    position: absolute;
    width: 61.9375em;
    height: 100%;
    bottom: 0;
    right: 0;
    background-image: url(../img/banner-round.svg);
    background-size: 100% auto;
    background-position: right bottom;
    background-repeat: no-repeat;
    overflow: hidden;
    img{
      left: 8.44em;
      bottom: 0;
      width: 39.625em;
      display: block;
      position: absolute;
    }
  }
  &__content{
    max-width: 39em;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex-grow: 1;
  }
  &__info{
    padding: 1em 0;
  }
  &__title{
    font-size: 2.6875em;
    line-height: 130%;
    font-weight: 700;
    span{
      color: $primary;
    }
  }
  &__prices{
    margin-top: 4.38em;
    @include grid100gap(.69em);
  }
  &__price2{
    font-size: 1.25em;
    line-height: 120%;
    text-decoration: line-through;
  }
  &__price{
    font-size: 2.5em;
    line-height: 120%;
    font-weight: 700;
    color: $primary;
  }
  &__nav{
    display: flex;
  }
}

.advantages{
  display: none;
  &__grid{
    .header-advantage{
      &__icon{
        width: 1.75em;
        margin-right: .25em;
      }
      .text15{
        font-size: .875em;
      }
    }
  }
}

.categories-index{
  padding: 1.3em 0 3em;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__grid{
    margin-top: 3.125em;
  }
}

.category-item{
  display: block;
  padding: 2.31em 1.875em;
  background-color: #3C4E86;
  color: #fff;
  border-radius: .1875em;
  &:hover{
    color: #fff;
    .text22{
      opacity: .8;
    }
    .category-item__img img{
      transform: scale(1.05);
    }
  }
  @media screen and (min-width: 993px){
    &._big{
      grid-column: span 2;
    }
    &._right{
      .category-item__img{
        justify-content: flex-end;
      }
    }
  }
  &__img{
    height: 19.375em;
    @include center;
    img{
      transition: all .3s;
      max-height: 100%;
      max-width: 100%;
      display: block;
    }
  }
  &__content{
    margin-top: 1.25em;
    @include grid100gap(.875em);
  }
  &__nums{
    display: flex;
  }
  &__price{
    padding: 0 .75em;
    height: 2.0625em;
    border-radius: 0.125em;
    background: #FFF;
    font-weight: 700;
    color: $black;
    display: flex;
    align-items: center;
    white-space: nowrap;
  }
}


@media screen and (max-width: 992px){
  .banners{
    padding: 0 0 .8em;
    .container{
      padding: 0;
    }
    .swiper{
      &-nav{
        display: none;
      }
      &-pagination{
        margin-top: .75em;
      }
    }
  }

  .banner{
    min-height: 26.25em;
    padding: 3.06em .92em 3.62em;
    &__img{
      width: 12.25em;
      height: 17.0625em;
      background-image: url(../img/banner-round2.svg);
      overflow: unset;
      img{
        width: 10.75em;
        left: unset;
        right: 0;
        bottom: 1.56em;
      }
    }
    &__content{
      max-width: 16.5em;
    }
    &__title{
      font-size: 1.5em;
      line-height: 117%;
      span{
        display: block;
        line-height: inherit;
      }
    }
    &__date{
      font-size: .875em;
      font-weight: 400;
    }
    &__info{
      padding: 1.875em 0;
      margin-bottom: auto;
    }
    &__prices{
      margin-top: 1.875em;
      gap: .31em;
    }
    &__price2{
      font-size: .875em;
    }
    &__price{
      font-size: 1.5em;
    }
    &__nav{
      .m-btn{
        padding: 0 1.25em;
        height: 2.5625em;
        span{
          font-size: .875em;
        }
      }
    }
  }

  .advantages{
    padding: .8em 0;
    display: block;
    &__grid{
      display: grid;
      grid-template-columns: 1fr 1fr 1fr;
      gap: 1.25em;
    }
  }

  ._section{
    .container{
      padding-bottom: 4.125em;
      position: relative;
    }
    &__btn{
      position: absolute;
      left: 50%;
      bottom: 0;
      transform: translateX(-50%);
    }
    &__top{
      display: block;
      text-align: center;
    }
  }

  .categories-index{
    padding: 3em 0 1.56em;
    &__grid{
      margin-top: 1.56em;
    }
  }

  .category-item{
    padding: 1.06em;
    &__img{
      height: 6em;
    }
    &__content{
      margin-top: .75em;
      gap: .5em;
    }
    &__price{
      padding: 0 .375em;
      height: 1.5em;
    }
    ._hide-mob{
      display: none;
    }
  }
}


@media screen and (max-width: 576px){
  .advantages{
    &__grid{
      grid-template-columns: 1fr .85fr;
    }
  }
}
