.cart{
  display: grid;
  grid-template-columns: calc(100% - 25.4375em - 1.875em) 25.4375em;
  gap: 1.875em;
  margin-top: 4.44em;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__sidebar{
    padding-top: 5.4675em;
  }
}

.cart-table{
  margin-top: 2.56em;
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 46.5em) 5.625em 6.125em 5.625em 10.125em;
    justify-content: space-between;
    gap: 4.25em;
  }
  &__th{
    font-size: 0.875em;
    line-height: 121%;
    color: $gray;
  }
  &__rows{
    margin-top: .56em;
    @include grid100gap(.5em);
  }
}

.cart-item,
.cart-item2{
  border-radius: 0.1875em;
  background: #F6F6F6;
  &__info{
    display: flex;
    align-items: center;
  }
  &__img{
    width: 6.25em;
    height: 6.25em;
    flex-shrink: 0;
    @include center;
    margin-right: 1.5em;
    img{
      max-width: 100%;
      max-height: 100%;
      display: block;
    }
  }
  &__btn{
    width: 1.5em;
    flex-shrink: 0;
    @include center;
    svg{
      width: 100%;
      height: auto;
    }
    &:hover{
      path{
        stroke: $primary;
      }
    }
  }
}

.cart-item{
  padding: .625em 0;
  align-items: center;
  &__title-mob{
    display: none;
  }
  &__info{
    padding: 0 .625em;
  }
  &__content{
    @include grid100gap(.75em);
  }
  &__prices{
    @include grid100gap(.625em);
  }
  &__price1,
  &__price2{
    font-size: 1em;
    line-height: 119%;
  }
  &__price2{
    text-decoration: line-through;
  }
  &__action{
    display: flex;
    align-items: center;
  }
  .compare-btn,
  .favourite-btn{
    margin-right: 1.875em;
  }
}

.cart-color{
  display: flex;
  align-items: center;
  &__items{
    display: flex;
    align-items: center;
    margin-left: .75em;
  }
}

.color-item{
  position: relative;
  @include lastM(0 .625em 0 0);
  input:checked~.color-item__inner{
    outline: 2px solid #000 !important;
  }
  &__inner{
    width: 1.25em;
    height: 1.25em;
  }
}

.cartcalc{
  width: 6.125em;
  max-width: 100%;
  button{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: .9375em;
    @include center;
    svg{
      width: 100%;
      height: auto;
    }
    &:hover{
      path{
        stroke: $black;
      }
    }
  }
  .ccalc-minus{
    left: .75em;
  }
  .ccalc-plus{
    right: .75em;
  }
  input{
    font-weight: 700;
    text-align: center;
    padding: 0 2em;
  }
}

.remove-btn,
.compare-btn,
.favourite-btn{
  &._added{
    .compare-btn__icon,
    .favourite-btn__icon{
      path{
        stroke: $primary;
      }
    }
  }
  &:not(._added):hover{
    path{
      stroke: $black;
    }
  }
  &__icon{
    width: 1.5em;
    flex-shrink: 0;
    @include center;
    svg{
      width: 100%;
      height: auto;
    }
  }
}

.favourite-btn{
  &._added{
    .favourite-btn__icon{
      path{
        fill: $primary;
      }
    }
  }
}

.cart-nav{
  margin-top: 2em;
  display: grid;
  grid-template-columns: calc(100% - 26.9375em) 25.4375em;
  gap: 1.5em;
}

.cart-add{
  display: flex;
  align-items: center;
  &__title{
    flex-shrink: 0;
    margin-right: 1.5em;
  }
}

.cart-search{
  flex-grow: 1;
  position: relative;
  z-index: 3;
  &._active{
    .cart-search__dropdown{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__dropdown{
    position: absolute;
    box-shadow: $boxShadow;
    padding: 1.25em;
    background-color: #fff;
    top: 100%;
    left: 0;
    width: 100%;
    margin-top: .75em;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
  }
  &__items{
    @include grid100gap(.5em);
  }
}

.search-field{
  input{
    padding-left: 3.44em;
    padding-right: 7em;
    &:focus{
      &~.search-field__icon{
        path{
          stroke: $black;
        }
      }
    }
  }
  .hint,
  &__icon{
    position: absolute;
    left: 1.56em;
    top: 50%;
    transform: translateY(-50%);
  }
  .hint{
    z-index: 3;
  }
  &__icon{
    width: 1.25em;
    height: auto;
  }
  &__btn{
    position: absolute;
    top: 50%;
    right: 1.56em;
    transform: translateY(-50%);
  }
}

.promo{
  position: relative;
  z-index: 3;
}

.cart-item2{
  padding: .625em 1.25em .625em .625em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__btn{
    margin-left: 1em;
    flex-shrink: 0;
  }
  &__img{
    width: 4.125em;
    height: 4.125em;
  }
}

.cart-sum{
  position: sticky;
  top: 1em;
  @include grid100gap(1.5em);
  &__props{
    @include grid100gap(1em);
  }
  &__items{
    @include grid100gap(.5em);
  }
  &__item{
    padding: .3125em .625em;
    border-radius: 0.1875em;
    background-color: $gray3;
    font-weight: 700;
    .text16{
      @include truncate(1);
    }
  }
}

.cart-prop{
  display: flex;
  justify-content: space-between;
  &:not(:last-child){
    padding-bottom: 1em;
    border-bottom: 1px solid #D9D9D9;
  }
}

.cart-collection{
  padding: 2.625em 0 5.3125em;
}

.collection{
  overflow: hidden;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .tabs{
    margin-top: 2.44em;
  }
  &__content{
    margin-top: 3.25em;
  }
  &__add-to-compare{
    width: 100%;
    margin-top: 3em;
  }
}

.collection-slider{
  .swiper-container{
    @media screen and (min-width: 993px){
      padding-bottom: 1.5em;
    }
  }
}

.product-card{
  padding: .625em;
  position: relative;
  &:hover{
    background-color: #fff;
    z-index: 3;
    .product-card__nav{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__top{
    position: relative;
  }
  &__img{
    display: block;
    position: relative;
    padding-bottom: 103.6%;
    img{
      @include centerAbsolute;
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__badges{
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    .m-badge{
      margin-right: .3125em;
    }
  }
  &__action{
    position: absolute;
    top: 0;
    right: 0;
    display: flex;
    .compare-btn{
      margin-right: 1.25em;
    }
  }
  &__prices{
    display: flex;
    align-items: center;
    margin-top: 1.875em;
  }
  &__price1{
    font-size: 1.1875em;
    line-height: 121%;
    font-weight: 700;
  }
  &__price2{
    margin-left: .625em;
    font-size: 1.0625em;
    line-height: 118%;
    text-decoration: line-through;
  }
  &__info{
    margin-top: 1.0625em;
    @include grid100gap(.69em);
  }
  &__title{
    font-size: 1em;
    line-height: 120%;
    @include truncate(1);
  }
  &__split{
    display: flex;
    align-items: center;
    .hint-btn{
      margin-left: .5em;
    }
    &-icon{
      width: 1.5em;
      flex-shrink: 0;
      margin-right: .31em;
    }
  }
  &__bottom{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 1.0625em;
  }
  &__nav{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .5em));
    gap: 1em;
    padding-top: 2em;
    position: absolute;
    top: 99%;
    left: 0;
    width: 100%;
    opacity: 0;
    pointer-events: none;
    .m-btn-black-outline:not(:hover){
      background-color: #fff;
    }
  }
}

.m-badge{
  padding: 0 .25em;
  min-width: 2.375em;
  height: 1.5625em;
  @include center;
  background-color: $black;
  color: #fff;
  span{
    font-size: .875em;
    line-height: 120%;
  }
  &._red{
    background-color: $primary;
  }
  &._blue{
    background-color: #4B6C8D;
  }
  &._blue2{
    background-color: #3670A9;
  }
  &._light-blue{
    background-color: #3A98CD;
  }
  &._green{
    background-color: $green;
  }
  &._orange{
    background-color: #F3630F;
  }
  &._yellow{
    background-color: #FFBA17;
  }
}

.hint-btn{
  width: 1em;
  svg{
    width: 100%;
    height: auto;
  }
  &:hover{
    path{
      stroke: $black;
    }
  }
}

.review-stat{
  display: flex;
  align-items: center;
  &__item{
    display: flex;
    align-items: center;
    @include lastM(0 1.25em 0 0);
    img{
      width: 1.25em;
      flex-shrink: 0;
      margin-right: .3125em;
    }
  }
}

.available-status{
  font-size: .875em;
  line-height: 120%;
  color: #469064;
  &._blue{
    color: #4B6C8D;
  }
  &._purple{
    color: #A357D2;
  }
}


@media screen and (max-width: 992px){
  .collection{
    padding: 1.56em 0;
    background-color: $gray3 !important;
    .tabs{
      display: inline-flex;
      position: absolute;
      right: .9375em;
      top: 0;
      margin: 0;
      &__dropdown{
        width: calc(100vw - 1.875em);
        left: unset;
        right: 0;
      }
    }
    &__add-to-compare,
    &__content{
      margin-top: 1.56em;
    }
    ._hide-mob{
      display: none;
    }
  }

  .collection-slider{
    .swiper{
      &-container{
        width: 100%;
      }
      &-slide{
        width: 14.375em;
        padding-right: .625em;
      }
    }
  }

  .product-card{
    padding: .75em;
    background-color: #fff;
    &__action,
    &__badges{
      flex-direction: column;
      align-items: flex-start;
    }
    &__badges{
      .m-badge{
        @include lastM(0 0 .19em);
      }
    }
    .compare-btn{
      margin: 0 0 .625em;
    }
    &__prices{
      margin-top: .94em;
    }
    &__price1{
      font-size: .875em;
    }
    &__price2{
      font-size: .8125em;
      margin-left: .75em;
    }
    &__info{
      margin-top: .75em;
      gap: .75em;
    }
    &__title{
      -webkit-line-clamp: 2;
      font-size: .875em;
    }
    &__split{
      display: none;
    }
    &__bottom{
      margin-top: .75em;
      flex-direction: column;
      align-items: flex-start;
    }
    .review-stat{
      margin-bottom: .75em;
    }
    &__nav{
      position: static;
      margin-top: .75em;
      padding: 0;
      opacity: 1;
      pointer-events: unset;
      grid-template-columns: repeat(2,calc(50% - .125em));
      gap: .25em;
      .m-btn{
        padding: 0 .25em;
        height: 1.75em;
        span{
          font-size: .625em;
        }
      }
    }
  }

  .m-badge{
    height: 1.3125em;
    span{
      font-size: .6875em;
    }
  }

  .remove-btn,
  .compare-btn,
  .favourite-btn{
    &__icon{
      width: 1.25em;
    }
  }

  .review-stat{
    &__item{
      @include lastM(0 .625em 0 0);
      img{
        margin-right: .13em;
      }
      span{
        font-size: .75em;
      }
    }
  }

  .available-status{
    font-size: .8125em;
  }

  .cart{
    margin-top: 1.875em;
    grid-template-columns: 100%;
    &__sidebar{
      padding-top: 0;
    }
  }

  .cart-table{
    margin-top: 1.75em;
    &__grid{
      grid-template-columns: calc(100% - 23.625em) 5.625em 6.125em 5.625em 2.25em;
      gap: 1em;
    }
  }

  .cart-item,
  .cart-item2{
    &__img{
      width: 4em;
      height: 4em;
      margin-right: .5em;
    }
  }

  .cart-item{
    padding: .5em 0;
    &__content{
      gap: .375em;
    }
    &__info{
      padding: 0 0 0 .5em;
      align-items: flex-start;
    }
    &__action{
      flex-direction: column;
      align-items: flex-start;
    }
    .compare-btn,
    .favourite-btn{
      margin: 0 0 .625em;
    }
    &__price1,
    &__price2,
    .text16{
      font-size: .875em;
    }
    .text14{
      font-size: .8125em;
    }
  }

  .color-item{
    @include lastM(0 .375em 0 0);
    &__inner{
      width: 1em;
      height: 1em;
    }
  }

  .cart-nav{
    grid-template-columns: 100%;
    gap: .75em;
    margin-top: 1.5em;
  }

  .cart-search{
    &__dropdown{
      padding: .5em;
    }
    .text15{
      font-size: .875em;
    }
  }

  .search-field{
    &__icon{
      width: 1em;
      left: 1em;
    }
    input{
      padding-left: 3em;
      padding-right: 5em;
    }
    &__btn{
      right: 1em;
      display: flex;
      align-items: center;
      span{
        font-size: .75em;
      }
    }
  }

  .cart-sum{
    gap: 1em;
    .text16{
      font-size: .875em;
    }
  }
}


@media screen and (max-width: 576px){
  .cart{
    &__top{
      display: block;
    }
    &__clear{
      margin-top: .75em;
    }
  }

  .cart-table{
    &__head{
      display: none;
    }
  }

  .cart-item{
    grid-template-columns: 1fr 1.25fr 1fr;
    gap: 1em .5em;
    align-items: flex-start;
    padding: .5em 2.5em .5em .5em;
    position: relative;
    &__info{
      grid-column: 1/-1;
      padding: 0;
    }
    &__action{
      position: absolute;
      top: .5em;
      right: .5em;
    }
    &__title-mob{
      display: block;
      font-size: .8125em;
      margin-bottom: .25em;
    }
  }

  .cart-add{
    display: block;
    &__title{
      margin: 0 0 .25em;
      font-size: .875em;
    }
  }

  .product-card{
    padding: 0;
    &__nav{
      grid-template-columns: 100%;
    }
  }

  .collection-slider{
    .product-card{
      padding: .5em;
    }
  }
}
