.header{
  &._show-menu{
    .menu{
      opacity: 1;
      pointer-events: unset;
    }
    .catalog-btn{
      ._view1{
        opacity: 0;
      }
      ._view2{
        opacity: 1;
      }
    }
  }
}

.header{
  &._show-cart{
    .header-cart{
      opacity: 1;
      pointer-events: unset;
      transform: translateX(0);
    }
  }
}

.menu{
  transition: all .3s;
  opacity: 0;
  pointer-events: none;
  z-index: 10;
  position: absolute;
  top: 11.875em;
  left: 0;
  width: 100%;
  //max-height: calc(100 * var(--vh) - 13.875em);
  overflow-y: auto;
  background-color: #fff;
  box-shadow: 0 4.375em 3.125em 0 rgba(0, 0, 0, 0.10);
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 28.3125em) 26.3125em;
    gap: 2em;
  }
  &__sidebar{
    padding: 2.5em 0;
  }
  &__content{
    display: grid;
    grid-template-columns: 23.25em calc(100% - 28.25em);
    gap: 5em;
  }
  &__items{
    border-right: 1px solid #EEE;
    @include grid100gap(.5em);
    padding: 2.5em 0;
    position: relative;
  }
  &__items2{
    margin-top: 2.5em;
  }
  &__links{
    display: flex;
    margin-bottom: 4em;
  }
  &__link{
    margin-right: .94em;
    padding: 0 1em;
    height: 2.0625em;
    font-weight: 700;
    span{
      font-size: .875em;
    }
    img{
      width: 1.125em;
      margin-right: .625em;
    }
  }
}

.menu-item{
  &._active .menu-item__show,
  &__show:hover{
    background: #F5F5F5;
    color: $primary;
    path{
      stroke: $black;
    }
  }
  &__show{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: .5em 1em;
    transition: all .3s;
    cursor: pointer;
    svg{
      width: 1em;
      height: auto;
    }
  }
  &__title{
    font-size: 1em;
    line-height: 119%;
    text-decoration: underline;
    font-weight: 700;
  }
}

.submenu{
  display: none;
  &__back{
    display: none;
  }
  &__grid{
    margin-top: 3.06em;
    column-count: 3;
    column-gap: 5em;
  }
  &__group{
    display: inline-flex;
    align-items: flex-start;
    flex-direction: column;
    width: 100%;
    @include lastM(0 0 2.44em);
    margin-bottom: 2.44em;
  }
  &__title{
    font-size: 1.125em;
    line-height: 120%;
    font-weight: 700;
    &:not(:first-child){
      margin-top: 1.33em;
    }
  }
  &__links{
    margin-top: .94em;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    color: #797979;
    @include lastM(0 0 .75em);
    span{
      font-size: .9375em;
      line-height: 120%;
    }
  }
}

.sale-card{
  padding: 0 1.81em 3.06em;
  background-color: $black;
  border-radius: .1875em;
  overflow: hidden;
  position: relative;
  display: block;
  &:hover{
    transform: scale(1.01);
  }
  &:before{
    content: '';
    position: absolute;
    top: -18.75em;
    left: 50%;
    transform: translateX(-50%);
    width: 39em;
    height: 39em;
    border-radius: 50%;
    background-color: $primary;
  }
  &__img{
    height: 26em;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    position: relative;
    z-index: 2;
    margin-bottom: 1.06em;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__title{
    font-size: 1.625em;
    line-height: 131%;
    font-weight: 700;
    color: #fff;
    span{
      color: $primary;
    }
  }
  &__badges{
    padding: 1.94em 1.81em;
    display: flex;
    justify-content: space-between;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 2;
    .m-badge{
      color: $black;
      &._red-text{
        color: $primary;
      }
    }
  }
}

.header-cart{
  color: $black;
  position: absolute;
  right: 0;
  top: 100%;
  margin-top: .5em;
  border-radius: 0.1875rem;
  background: #FFF;
  width: 40.75em;
  padding: 1.25em;
  box-shadow: 0 2.375em 5.125em 0 rgba(0, 0, 0, 0.10);
  transition: all .3s;
  opacity: 0;
  pointer-events: none;
  z-index: 10;
  &__items{
    @include grid100gap(.5em);
  }
  &__bottom{
    margin-top: 1.5em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__close{
    display: none;
  }
}

.cart-preview{
  display: grid;
  grid-template-columns: 4.125em calc(100% - 23.375em) 5.625em 6.125em 1.5em;
  align-items: center;
  gap: 1.5em;
  border-radius: 0.1875em;
  background: #F6F6F6;
  padding: .625em;
  &__img{
    height: 4.125em;
    background-color: #fff;
    @include center;
    img{
      max-width: 100%;
      max-height: 100%;
      display: block;
    }
  }
}

.city-modal{
  .modal{
    &__dialog{
      width: 52.875em;
    }
    &__top{
      text-align: left;
    }
  }
  &__search{
    margin-top: 2em;
    @include grid100gap(1em);
  }
  &__items{
    margin-top: 2.625em;
    column-count: 3;
    column-gap: 4.56em;
  }
  &__item{
    display: inline-block;
    width: 100%;
    font-size: 1em;
    line-height: 150%;
    font-weight: 700;
    text-decoration: underline;
    transition: all .3s;
    cursor: pointer;
    @include lastM(0 0 .875em);
    &:hover,
    &._active{
      color: $primary;
    }
    &._active{
      font-weight: 800;
    }
  }
}




@media screen and (max-width: 992px){
  .header-cart{
    transform: translateX(-120%);
    z-index: 20;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    right: unset;
    margin: 0;
    border-radius: 0;
    &__close{
      display: flex;
      margin: 0 0 1.5em auto;
      width: 1.5em;
      svg{
        width: 100%;
        height: auto;
      }
      &:hover{
        path{
          stroke: $primary;
        }
      }
    }
  }

  .cart-preview{
    position: relative;
  }

  .menu{
    position: static;
    opacity: 1;
    pointer-events: unset;
    box-shadow: unset;
    border-bottom: 1px solid #eee;
    display: none;
    &__inner{
      grid-template-columns: 100%;
    }
    &__sidebar{
      display: none;
    }
    &__content{
      grid-template-columns: 100%;
      gap: 0;
    }
    &__items{
      border: none;
      padding: 0;
      gap: 0;
    }
    &__items2{
      margin: 1em 0 0;
      padding: 0;
    }
    &__links{
      flex-wrap: wrap;
      padding: 0 2em;
      margin-bottom: 1.5em;
    }
    &__link{
      margin-top: .5em;
    }
  }

  .menu-item{
    border-bottom: 1px solid #eee;
    &._active{
      .menu-item__show{
        color: $black;
        svg{
          transform: rotate(90deg);
        }
      }
    }
    &__show{
      padding: 1em 2em;
      &:hover{
        color: $black;
      }
    }
  }

  .submenu{
    border-bottom: 1px solid #eee;
    .text24{
      display: none;
    }
    &__grid{
      margin-top: 0;
      column-count: 1;
      column-gap: 1.25em;
      padding: 1em 2em;
    }
    &__group{
      margin-bottom: 0;
      @include lastM(0 0 1em);
    }
    &__title{
      font-size: .875em;
    }
    &__links{
      margin-top: .75em;
    }
    &__link{
      @include lastM(0 0 .5em);
      span{
        font-size: .75em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .cart-preview{
    grid-template-columns: 100%;
    gap: .5em;
    &__img{
      max-width: 4.125em;
    }
  }

  .city-modal{
    &__search{
      margin-top: 1.25em;
      gap: .5em;
    }
    &__items{
      margin-top: 1.5em;
      column-count: 2;
      column-gap: 1em;
    }
  }
}
