.reviews-collection{
  padding: 4.56em 0 3.56em;
  background-color: $gray3;
  overflow: hidden;
  .review-card{
    height: 100%;
  }
}

.reviews-slider{
  margin-top: 3.125em;
}

.history{
  padding: 4.6em 0;
  .text{
    margin-top: 2em;
  }
  &__items{
    margin-top: 3em;
  }
}

.history-card{
  display: grid;
  grid-template-columns: 7.3em calc(100% - 23.6125em - 3.75em) 16.3125em;
  gap: 1.875em;
  align-items: flex-start;
  &:not(:last-child){
    padding-bottom: 2em;
    margin-bottom: 2em;
    border-bottom: 1px solid #EFEFEF;
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  .text{
    margin-top: 1em;
  }
  &__year{
    @include center;
    width: 6em;
    height: 2.625em;
    border-radius: 3.125em;
    background-color: $black;
    color: #fff;
    font-weight: 700;
  }
}


@media screen and (max-width: 992px){
  .vacancies-collection,
  .reviews-collection{
    padding: 1.56em 0;
    overflow: hidden;
  }

  .vacancies-slider,
  .reviews-slider{
    margin-top: 1.56em;
    .swiper{
      &-container{
        overflow: unset;
        width: 100%;
      }
      &-slide{
        width: 17.375em;
        padding-right: .625em;
      }
    }
  }

  .review-card{
    padding: .9375em;
    &__top{
      position: relative;
    }
    .avatar{
      width: 2.5em;
      height: 2.5em;
    }
    &__site{
      position: absolute;
      right: 0;
      bottom: 0;
      .line-btn{
        span{
          font-size: .8125em;
        }
      }
      &-split{
        margin: 0 .3em;
      }
    }
    &__text{
      margin-top: .94em;
    }
  }

  .small-rating{
    img{
      width: 1.125em;
    }
    .text14{
      font-size: .8125em;
    }
  }

  .history{
    padding: 2em 0;
    .text{
      margin-top: 1em;
    }
  }

  .history-card{
    grid-template-columns: 100%;
    gap: 1em;
    &:not(:last-child){
      padding-bottom: 1em;
      margin-bottom: 1em;
    }
    &__content{
      order: 2;
    }
    &__img{
      max-width: 30em;
    }
  }
}
